*,
::after,
::before {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  outline: 0
}
:root {
  font-size: 62.5%
}
.outer-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  overflow-x: hidden;
  min-height: 100vh
}
.wrapper {
  -webkit-box-flex: 1;
  -ms-flex: 1 0 auto;
  flex: 1 0 auto;
  min-height: 100vh;
  @include respond(tabdesk) {
    padding-bottom: 6.4rem;
  }
  @include respond(mobilemini) {
    padding-bottom: 3.2rem;
    min-height: auto;
  }
}

.wrapper__dashboard {
  display: flex;
  align-items: flex-start;

  @include respond(desktop) {
    
  }

  @include respond(tabdesk) {
    padding-bottom: 0;
  }

  @include respond(mobilemini) {
    display: block;
  }

}

.confirm-wrapper {
  padding-bottom: 0
}

.custom-container {
  @include respond(desktop-widths) {
    max-width: 100%;
  }

  @include respond(mobilemini) {
    padding-right: 0;
    padding-left: 0;
  }
}

.icon-svg {
  color: #8C96A2;
}

.icon-right {
  color: $bckg-blue;

  @include respond(mobilemini) {
    color: #8C96A2;
  }
}

.btn {
  border: 0 none;
  outline: 0;
  padding-top: 8px;
  padding-bottom: 8px;
  color: #fff;
  border-radius: 5px;
  font-size: 13px;
  font-weight: 700;
  white-space: nowrap;
}

body,
html {
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  font-size: 1.6rem;
  color: #182e45
}
img {
  max-width: 100%;
  max-height: 100%
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  font-weight: 700;
}
li,
ul {
  list-style: none;
  margin: 0;
  padding: 0
}
a:hover {
  text-decoration: none
}
a {
  text-decoration: none;
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s
}