/* ------------------ Footer-page ------------------ */

.site-footer {
    padding-bottom: 2.4rem;

    @include respond(mobilemini) {
      padding-bottom: 16px;
    }
  }
  
  .site-footer__contact {
    margin-bottom: 24px;

    @include respond(mobilemini) {
      flex-direction: column;
      padding: 12px 0;
    }
  }
  
  .site-footer__contact {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $bckg-bottom;
    padding: 8px 0;
    border-radius: 5px;
  }
  
  .site-footer__info {
    display: flex;
    align-items: center;
    transition: all 0.3s ease-in-out;
    margin-right: 16px;

    @include respond(mobilemini) {
      margin-right: 0;
      margin-bottom: 4px;
    }
  }
  
  .site-footer__info:hover {
    color: $bckg-blue;
    text-decoration: none;
  }
  
  .site-footer__info:hover .site-footer__icon path {
    fill: $bckg-blue;
  }
  
  .site-footer__icon {
    width: 16px;
    height: 16px;
    margin-right: 16px;
    flex-shrink: 0;

    @include respond(mobilemini) {
      margin-right: 8px;
    }
  }
  
  .site-footer__email {
    display: block;
    color: $default-color;
  }
  
  .site-footer__text {
    display: inline-block;
    font-size: 13px;
    color: $text-color;
  }
  
  .site-footer__bottom {
    font-size: 11px;
    line-height: 13px;
    color: $text-color;
  }

  /* dashboard-footer */

  .site-footer__dashboard {
    padding-bottom: 0;
  }