.sidebar-menu {
  width: 27.9rem;
  background-color: #ffffff;
  min-height: 100vh;
  position: absolute;
  z-index: 999;
  top: 0;
  left: 0;
  border-right: 1px solid $border;
  transition: width 0.3s;
  @include respond(desktop) {
    width: 6rem;
  }
  
  @include respond(phone) {
    display: none;
  }
}

.sidebar-menu.open {
  width: 27.9rem
}

.sidebar-menu.open .dashboard-menu__text {
  display: block;
}

.sidebar-menu.open  .sidebar-menu-logo > .site-logo {
  display: block;
}

.sidebar-menu.open  .sidebar-menu-logo > .snp-logo__sidebar {
  display: none;
}

.sidebar-menu.open  .dashboard-menu__icon_open {
  transform: rotate(180deg);
}

.account-user__sidebar {
  display: none !important;
}

.sidebar-menu-logo .site-logo {
    @include respond(desktop) {
      display: none;
    }
}

.sidebar-menu-logo .snp-logo__sidebar {
    width: 2.1rem;
    height: 2.1rem;
    @include respond(desktop-widths) {
      display: none;
    }
}

/* sidebar-menu__head */

.sidebar-menu__head {
  margin-bottom: 4rem;
  padding: 18px 32px;
  border-bottom: 1px solid $border;

  @include respond(desktop) {
    margin-bottom: 2rem;
    padding: 16px 20px;
  }

  @include respond(tabdesk) {
    padding: 18px 20px;
  }
}

/* sidebar-menu__body */

.company-info {
  display: block;
  padding: 0 3.2rem 4rem;
  border-bottom: 1px solid $border;
  margin-bottom: 4rem;

  @include respond(desktop) {
    display: none;
  }
}

// .sidebar-menu__body {

// }

.company-info__item {
  margin-bottom: 2.4rem;
}

.company-info__link {
  color: $text-color;
  font-size: 14px;
  display: inline-block;
}

.company-info__icon {
  margin-left: 8px;
  color: $bg-green;
  display: none;
}

.company-info__item.active .company-info__icon {
  display: inline-block;
}

/* company-info__content */


.company-info__save {
  background-color: $btn-bg;
  color: $bckg-blue;
  padding-right: 4.3rem;
  padding-left: 4.3rem;
  border: 1px solid $btn-bg;
  margin-bottom: 2.4rem;
}

.company-info__restart {
  border: 1px solid rgba(23, 85, 153, 0.3);
  padding-right: 2.5rem;
  padding-left: 2.5rem;
  color: $bckg-blue;
  margin-bottom: 2.4rem;
}

.company-info__percent {
  border: 1px solid #b9cce0;
  border-radius: 5px;
  font-size: 1rem;
  padding: 4px 0;
  text-align: center;
}

/* dashboard-menu */

.dashboard-menu__item {
  margin-bottom: 3.2rem;
  padding: 0px 32px 0;

  @include respond(desktop) {
    padding-right: 2.2rem;
    padding-left: 2.2rem;
    margin-bottom: 4rem;
  }
}

.dashboard-menu__item_next {
    @include respond(desktop) {
        margin-bottom: 0;
        padding-top: 22px;
        padding-bottom: 22px;
        border-top: 1px solid $border;
        margin-top: 17rem;
    }

    @include respond(desktop-widths) {
      display: none;
    }
}

.icon-reports {
  border-radius: 4px;
  border: 2px solid #8c96a2;
}

.icon-reports:hover {
  color: $dashboard-hover;
}

.dashboard-menu__link {
  display: flex;
  align-items: center;
  color: $text-color;
  line-height: 24px;
  position: relative;
  transition: all 0.3s ease-in-out;

  @include respond(desktop) {
    line-height: 16px;
  }
}

.dashboard-menu__link::after {
  content: "";
  position: absolute;
  top: 0;
  right: -3.2rem;
  width: 6px;
  height: 24px;
  background-color: $color-red;
  opacity: 0;
  transition: all 0.3s ease-in-out;

  @include respond(desktop) {
    right: -2.2rem;
    top: -4px;
  }
}

.dashboard-menu__link:hover .dashboard-menu__icon {
  color: $dashboard-hover;
}

.dashboard-menu__item:hover .dashboard-menu__link::after {
  opacity: 1;
}

.dashboard-menu__text {
  font-size: 14px;
  display: inline-block;
  margin-left: 1.6rem;
  @include respond(desktop) {
    display: none;
  }
}

.dashboard-menu__text:hover {
  color: $text-color;
}


