/* -------------------- form-page --------------------- */

.form-page__wrapper {
    max-width: 57rem;
    margin-right: auto;
    margin-left: auto;
  }
  
  .form-page__title {
    font-size: 2.6rem;
    font-weight: 700;
    text-align: center;
    margin-bottom: 6.4rem;
    @include respond(mobilemini) {
      font-size: 18px;
      margin-bottom: 3.2rem;
    }
  }
  
  .form-page__wrp {
    position: relative;
  }
  
  .form-page__wrp:not(:last-child) {
    margin-bottom: 24px;
  }
  
  .form-page__label {
    margin: 0;
    margin-bottom: 8px;
    display: block;
    font-size: 18px;
    font-weight: 700;
    transition: all 0.3s;

    @include respond(mobilemini) {
      font-size: 15px;
    }
  }
  
  .form-page__required {
    color: #8C96A2;
    font-size: 12px;
    line-height: 2rem;
    display: inline-block;
    margin-left: 8px;
    opacity: 0;
    transition: all 0.3s;
  }
  
  .form-page__required_margin {
    margin-left: 3.3rem;
  }
  
  /* .form-page__label .form-page__required {
    opacity: 0;
  } */
  
  /* .form-page__label .required-active {
    opacity: 1;
  } */
  
  .form-page__question {
    position: absolute;
    width: 17px;
    height: 17px;
    background-color: #C7CED7;
    border-radius: 50%;
    top: 4px;
    left: 14.8rem;
    transition: all 0.3s ease-in-out;
    display: flex;
    align-items: center;
    justify-content: center;

    @include respond(mobilemini) {
      left: 13rem;
    }
  }
  
  .form-page__question.active {
    background-color: $bckg-blue;
  }
  
  .form-page__question.active .form-page__answer {
    opacity: 1;
    visibility: visible;
    backface-visibility: hidden;
    transition: opacity .4s ease-out, transform .5s cubic-bezier(0, 1, .5, 1);
    background-color: #182E45;
  }
  
  .form-page__question.active .form-page__answer::before {
    opacity: 1;
  }
  
  .icon-question {
    position: absolute;
    top: 4px;
    left: 5px;
  }
  
  .icon-question img {
    position: absolute;
    top: 50%;
    left: 50%
  }
  
  .form-page__answer {
    max-width: 250px;
    min-width: 250px;
    padding: 8px;
    font-size: 13px;
    line-height: 21px;
    font-weight: 700;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    border-radius: 3px;
    text-align: center;
    opacity: 0;
    position: absolute;
    left: 50%;
    bottom: 100%;
    z-index: 15;
    transform: translate(-50%, -8px);
    visibility: hidden;
    transition: visibility 0s .5s, opacity .3s ease-out, transform .5s cubic-bezier(0, 1, .5, 1), -webkit-transform .5s cubic-bezier(0, 1, .5, 1);
    backface-visibility: hidden;
    font-family: "Montserrat", sans-serif;
  }
  
  .form-page__answer::before {
    content: "";
    position: absolute;
    left: 50%;
    top: 100%;
    transform: translateX(-50%);
    border-color: #182E45 transparent;
    border-width: 5px 5px 0;
    border-style: solid;
    width: 0;
    border-radius: 0;
    opacity: 0;
  }
  
  .form-page__pole {
    position: relative;
  }

  
  
  .form-page__input {
    width: 100%;
    outline: 0;
    border: 0 none;
    /* border: 1px solid #B9CCE0; */
    box-shadow: inset 0 0 0 1px #b9cce0;
    border-radius: 5px;
    min-height: 48px;
    padding: 12px 32px 12px 12px;
  }
  
  .form-page__input:focus {
    box-shadow: inset 0 0 0 1px ($bckg-blue);
  }
  
  .form-page__input.error {
    box-shadow: inset 0 0 0 1px #E10909;
  }
  
  .form-page__pole .icon-error {
    position: absolute;
    width: 2rem;
    height: 2rem;
    background-color: #E10909;
    border-radius: 50%;
    right: 12px;
    top: 14px;
    text-align: center;
    color: #fff;
    opacity: 0;
  }
  
  .form-page__pole .icon-error__active {
    opacity: 1;
  }
  
  .form-page__pole .icon-done {
    background-image: url(../../images/src/icons/check.svg);
    background-size: 10px 8px;
    background-position: center center;
    background-repeat: no-repeat;
    background-color: #24B63B;
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
    position: absolute;
    top: 14px;
    right: 12px;
    opacity: 0;
  }
  
  .icon-done__check {
    position: absolute;
    top: 6px;
    left: 5px;
  }
  
  .form-page__pole .icon-done__active {
    opacity: 1;
  }
  
  /* view-error */
  
  .form-page__form {
    transition: all 0.3s;
  }
  
  .form-page__form.was-validated .form-page__input:invalid {
    box-shadow: inset 0 0 0 1px #E10909;
    padding-right: 12px;
    background-image: url(../images/src/icons/warning-red.svg);
    background-position: right 12px center;
    background-repeat: no-repeat;
    background-size: 20px 20px;
  }
  
  .form-page__form.was-validated .form-page__input:valid {
    padding-right: 12px;
    background-image: url(../images/src/icons/success.svg);
    background-position: right 12px center;
    background-repeat: no-repeat;
    background-size: 20px 20px;
  }
  
  
  .form-page__form.was-validated .form-page__input-confirm.active:valid {
    box-shadow: inset 0 0 0 1px #E10909;
    padding-right: 12px;
    background-image: url(../images/src/icons/warning-red.svg);
    background-position: right 12px center;
    background-repeat: no-repeat;
    background-size: 20px 20px;
  }
  
  .form-page__form.was-validated .form-page__input-confirm:valid {
    padding-right: 12px;
    background-image: url(../images/src/icons/success.svg);
    background-position: right 12px center;
    background-repeat: no-repeat;
    background-size: 20px 20px;
  }
  
  
  .form-page__form.was-validated .name-required:valid {
    box-shadow: inset 0 0 0 1px #E10909;
    padding-right: 12px;
    background-image: url(../images/src/icons/warning-red.svg);
    background-position: right 12px center;
    background-repeat: no-repeat;
    background-size: 20px 20px;
  }
  
  .form-page__form.was-validated .name-required.active:valid {
    box-shadow: inset 0 0 0 1px #b9cce0;
    padding-right: 12px;
    background-image: url(../images/src/icons/success.svg);
    background-position: right 12px center;
    background-repeat: no-repeat;
    background-size: 20px 20px;
  }
  
  
  .form-page__form.was-validated .form-page__input:valid + .form-page__label .form-page__required {
    opacity: 1;
  }
  
  .form-page__form.was-validated .form-page__label .form-page__required {
    opacity: 1;
  }
  
  /* checker-list */
  
  .checker-list {
    padding: 16px;
    max-width: 17.7rem;
    box-shadow: 0px 14px 34px rgba(17, 54, 95, 0.12);
    background-color: #fff;
    border-radius: 5px;
    opacity: 0;
    position: absolute;
    left: 0;
    margin-top: 4px;
    top: 100%;
    z-index: 15;
    transform: translateY(0%);
    visibility: hidden;
    transition: visibility 0s .5s, opacity .3s ease-out, transform .3s cubic-bezier(0, 1, .5, 1);
    backface-visibility: hidden;
  }
  
  .checker-list.active {
    opacity: 1;
    visibility: visible;
    backface-visibility: hidden;
    transition: opacity .4s ease-out, transform .5s cubic-bezier(0, 1, .5, 1);
  }
  
  .checker-list__item {
    color: $text-color;
    font-size: 12px;
    line-height: 2rem;
    text-align: left;
  }
  
  .checker-list__item .checker-list__icon {
    width: 12px;
    height: 12px;
    background-color: $disabled;
    border-radius: 50%;
    position: relative;
    display: inline-block;
    margin-right: 6px;
  }
  
  .checker-list__item.is-success .checker-list__icon {
    background-color: $bg-green;
  }
  
  .icon-check {
    position: absolute;
    top: 4px;
    left: 3px;
  }
  
  .checker-list:hover {
     opacity: 1;
     visibility: visible;
     backface-visibility: hidden;
     transition: opacity .4s ease-out, transform .5s cubic-bezier(0, 1, .5, 1);
  }
  
  .form-page__text {
    font-size: 14px;
    color: $text-color;
    line-height: 24px;
    text-align: center;

    @include respond(phone) {
      font-size: 12px;
    }
  }
  
  .form-page__text_link {
    display: inline-block;
    color: $bckg-blue;
  }
  
  .form-page__text_link:hover {
    text-decoration: underline;
    color: $bckg-blue;
  }
  
  .form-page__btn {
    width: 100%;
    padding-top: 16px;
    padding-bottom: 16px;
    color: #fff;
    font-size: 16px;
    border: 0 none;
    outline: 0;
    background-color: $bckg-blue;
  }
  
  .form-page__btn:disabled {
    background-color: $disabled;
    cursor: not-allowed;
  }
  
  .form-page__btn:disabled:hover {
    background-color: $disabled;
  }
  
  .form-page__btn:hover {
    background-color: #113c6b;
    color: #fff;
  }
  
  .form-page__text_login {
    font-size: 16px;

    @include respond(mobilemini) {
      font-size: 14px;
    }
  }