/* ------------------ confirm-page ----------------------- */

.confirm-page__wrp {
    max-width: 37rem;
    margin-right: auto;
    margin-left: auto;
    text-align: center;
  }
  
  .confirm-page__title {
    font-size: 18px;
    font-weight: 700;
    line-height: 29px;
    margin-bottom: 16px;
  }
  
  .confirm-page__text {
    font-size: 14px;
    color: $text-color;
    line-height: 24px;
    margin-bottom: 3.2rem;
  }
  
  .confirm-page__text_link {
    display: inline-block;
    color: $bckg-blue;
    transition: all 0.3s;
  }
  
  .confirm-page__text_link:hover {
    text-decoration: underline;
  }
  
  .confirm-page__code {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 3.2rem;
    flex-wrap: wrap;
  }
  
  .confirm-page__btn {
    font-size: 2rem;
    outline: 0;
    border: 0 none;
    box-shadow: inset 0 0 0 1px $border-color;
    background-color: #fff;
    height: 4.8rem;
    width: 4.8rem;
    border-radius: 5px;
    margin-right: 8px;
    text-align: center;
    font-size: 2rem;
    color: $default-color;
    font-weight: 500;
    cursor: auto !important;
  }
  
  .confirm-page__btn:disabled {
    cursor: not-allowed;
    background-color: #EBF0F4;
    box-shadow: inset 0 0 0 1px rgba(23, 85, 153, 0.05);
  }
  
  
  .form-page__form.was-validated .confirm-page__btn:valid {
    box-shadow: inset 0 0 0 1px #E10909;
  }
  
  .confirm-page__btn.error {
    box-shadow: inset 0 0 0 1px #E10909;
  }
  
  .form-page__form .confirm-page__error-text {
    font-size: 13px;
    line-height: 21px;
    color: $color-red;
    display: none;
    margin-top: 8px;
    margin-bottom: 3.2rem;
    transition: all 0.3s;
  }
  
  .form-page__form.was-validated .confirm-page__error-text {
    display: block;
  }
  
  .form-page__form.was-validated .confirm-page__code {
    margin: 0;
  }
  
  .confirm-page__btn:focus {
    box-shadow: inset 0 0 0 1px $bckg-blue;
  }
  
  .confirm-page__submit {
    width: 100%;
    min-height: 5.6rem;
    outline: 0;
    border: 0 none;
    background-color: $bckg-blue;
    color: #fff;
    font-size: 15px;
    line-height: 18px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .confirm-page__submit:disabled {
    background-color: $disabled;
    cursor: not-allowed;
  }
  
  .confirm-page__submit:disabled:hover {
    background-color: $disabled;
  }
  
  
  .submit-text {
    color: #fff;
    display: inline-block;
  }
  
  .confirm-page__submit .submit-text-none {
    display: none;
  }
  
  .confirm-page__submit:hover {
    background-color: $bg-hover;
  }
  
  .submit-icon {
    opacity: 0;
  }
  
  .confirm-page__submit .submit-text-none + .submit-icon {
    opacity: 1;
  }
  
  
  
  /* -------------------- confirm-done ---------------- */
  
  .confirm-done__inner {
    max-width: 57rem;
    margin-right: auto;
    margin-left: auto;
    text-align: center;
  }
  
  .confirm-done__check{
    margin-bottom: 3.2rem;
    width: 7.2rem;
    height: 7.2rem;
    background-color: $bg-green;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: auto;
    margin-left: auto;
  }
  
  .confirm-done__icon {
    width: 3.6rem;
    height: 2.7rem;
  }
  
  .confirm-done__title {
    margin-bottom: 3.2rem;
    font-weight: 700;
    font-size: 18px;
    line-height: 29px;
  }
  
  .confirm-done__text {
    display: block;
    font-size: 16px;
    color: $text-color;
    line-height: 24px;
  }
  
  .confrim-done__text_link {
    display: inline-block;
    color: $bckg-blue;
  }
  
  .confrim-done__text_link:hover {
    text-decoration: underline;
  }