#main {
  flex: 1 0 auto;

  @include respond(mobilemini) {
    flex: 1 1 auto;
  }
}

.header-1 {

  @include respond(desktop-widths) {
    margin-left: -4rem;
    margin-right: -4rem;
  }

  @include respond(lgdsf) {
    margin-bottom: 4rem;
    margin-left: -12rem;
    margin-right: -12rem;
  }

  @include respond(desktop) {
    padding-top: 8px;
    padding-bottom: 8px;
    margin-bottom: 2rem;
    margin-right: -2rem;
    margin-left: -2rem;
  }

  @include respond(tabdesk) {
    padding-top: 12px;
    padding-bottom: 12px;
  }

  @include respond(mobilemini) {
    margin-right: -1.6rem;
    margin-left: -1.6rem;
    padding-top: 5px;
    padding-bottom: 0px;
    position: relative;
  }

}

.page-header__dashboard {

  @include respond(mobilemini) {
    padding-bottom: 5px;
    padding-right: 1rem;
    padding-left: 1rem;
  }
  
}

.page-header__mobile {
  @include respond(mobilemini) {
    height: 4rem;
    background-color: #fff;
    border-top: 1px solid $border;
  }
}

.main-bg {
  background-color: $body-color;
}

.page-heaedr__left {
  @include respond(desktop-widths) {
    margin-right: auto;
  }
}

.account-view {
  margin-right: 4rem;

  @include respond(tabdesk) {
    margin-right: 0;
  }

}

.page-heaedr__left .account-view {
  @include respond(desktop) {
    display: none !important;
  }

  @include respond(tabdesk) {
    display: flex !important;
  }

  @include respond(mobilemini) {
    display: none !important;
  }
}

.account-view__title {
  margin-right: 1.6rem;
  font-size: 14px;
}

.account-view__count {
  display: flex;
  align-items: center;
}

.count-strong {
  margin: 0 5px;
}

.account-view__icon {
  flex-shrink: 0;
}

.icon-easy {
  width: 1.6rem;
  height: 1.6rem;
}

.account-view__count-icon {
  margin-right: 1.6rem;
}

.site-logo__tab {
  display: none;
  @include respond(tabdesk) {
    width: 14.6rem;
    height: 2.2rem;
    display: block;
  }

  @include respond(mobilemini) {
    width: 11.2rem;
    height: 1.7rem;
  }
}

/* right-header */

.account-user__activate {
  color: $bckg-blue;
  display: block;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: 3.2rem;
  background-color: $btn-bg;
  font-size: 13px;
  border: 1px solid $btn-bg;
  @include respond(desktop-widths) {
    display: none;
  }

  @include respond(tabdesk) {
    display: none;
  }

  @include respond(mobilemini) {
    display: block;
    margin-right: 0;
    padding-top: 5px;
    padding-bottom: 5px;
    line-height: 18px;
    bottom: 0;
    position: absolute;
    left: 50%;
    margin-left: -10.5rem;
    margin-bottom: 5px;
  }
}

.account-user__wrp {
  margin-right: 3.2rem;
  display: flex;
  align-items: center;
  @include respond(tabdesk) {
    display: none;
  }
}

.account-user__logo {
  width: 3.2rem;
  height: 3.2rem;
  flex-shrink: 0;
  margin-right: 8px;
}

.account-user__logo img {
  border-radius: 50%;
}
.account-user__name {
  font-size: 13px;
  line-height: 2.1rem;
  color: $text-color;
}

.account-user__message,
.account-user__moon,
.account-user__humburger {
  margin-right: 3.2rem;
  width: 3.2rem;
  height: 3.2rem;
  box-shadow: inset 0 0 0 1px $border-color;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;

  @include respond(tabdesk) {
    margin-right: 2.4rem;
  }

  @include respond(mobilemini) {
    margin-right: 1.6rem;
  }
}

.account-user__message::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: $color-red;
}

.account-user__humburger {
  display: none;
  @include respond(tabdesk) {
    display: block;
    margin-right: 0;
  }
}

.account-user__icon {
  width: 1.6rem;
  height: 1.6rem;
  flex-shrink: 0;
  position: absolute;
  top: 8px;
  left: 8px;
}
.account-user__logout {
  color: $light-color;
  font-size: 13px;
  line-height: 2.1rem;
  cursor: pointer;
  @include respond(tabdesk) {
    display: none;
  }
}

/* applications header */

.page-header__application {
  display: none;
}

.page-header__application.active {
  display: block;
}

.app-status__name {
  font-size: 13px;
  margin-right: 8px;
}

.app-status__box {
  display: flex;
  align-items: center;
  padding: 8px;
  background-color: $status-first;
  border-radius: 5px;
}

.app-status__text {
  color: #fff;
  font-size: 13px;
  line-height: 21px;
}

.app-status__icon {
  width: 14px;
  height: 14px;
  flex-shrink: 0;
  margin-right: 8px;
}

.app-status__box_orange {
  background-color: $status-red;
  display: none;
}

.app-status__box_orange.active {
  display: flex;
}

.app-status__box_red {
  background-color: $status-red;
  display: none;
}

.app-status__box_red.active {
  display: flex;
}
