/* --------------------- header-page -------------------------- */

.page-header {
  padding-top: 16px;
  padding-bottom: 16px;
  border-bottom: 1px solid #17579b1a;
  margin-bottom: 6.4rem;
  background-color: #fff;

    @include respond(mobilemini) {
        padding-top: 5px;
        padding-bottom: 5px;
        margin-bottom: 3.2rem;
    }
}

.page-headr__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.site-logo {
  display: inline-block;
  width: 180px;
  height: 28px;
  @include respond(mobilemini) {
    width: 12.4rem;
    height: 2rem;
  }
}

.btn {
  display: block;
  font-size: 13px;
  font-weight: 700;
  color: $bckg-blue;
  border: 1px solid rgba(23, 85, 153, 0.3);
  border-radius: 5px;
  transition: all 0.3s linear;
}

.page-header__btn {
  padding: 8px 34px;
  
  @include respond(mobilemini) {
          padding: 6px 23px;
          font-size: 11px;
  }
}

.page-header__btn:hover {
  background-color: $bckg-blue;
  color: #fff;
  text-decoration: none;
}