/* main-page */

.main-page {
    @include respond(desktop-widths) {
        margin-left: 4rem;
        margin-right: 4rem;
    }
    @include respond(lgdsf) {
        margin-left: 12rem;
        margin-right: 12rem;
    }

    @include respond(desktop) {
      margin-right: 2rem;
      margin-left: 2rem;
      padding-bottom: 0rem;
    }

    @include respond(phone) {
        padding-bottom: 9.6rem;
    }

    @include respond(mobilemini) {
      padding-bottom: 0rem;
      margin-right: 1.6rem;
      margin-left: 1.6rem;
    }
}

.blank-block {
    @include respond(desktop) {
      width: 6rem;
    }

    @include respond(desktop-widths) {
      width: 27.9rem;
    }

    @include respond(phone) {
      width: 0;
    }
}

.perfect-container {
    padding-right: 0;
    padding-left: 0;
}

.pg-section {
    padding: 4rem 0 5rem;
    background-color: #fff;
    box-shadow: 3px 3px 11px rgba(17, 54, 95, 0.06);
    border-radius: 5px;
    border: 1px solid $border;
    text-align: center;
}

.pg-section:not(:last-child) {
    margin-bottom: 2rem;

    @include respond(mobilemini) {
      margin-bottom: 1.6rem;
    }
}

/* welcome-page */

.welcome-page {
    @include respond(mobilemini) {
      padding-top: 1.6rem;
      padding-bottom: 1.6rem;
    }
}

.welcome-page__inner {
    max-width: 86rem;
    margin-right: auto;
    margin-left: auto;
    text-align: center;
}

.welcome-page__logo {
    display: table;
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 2.4rem;

    @include respond(mobilemini) {
      margin-bottom: 1.6rem;
    }
}

.snp-logo {
    width: 6rem;
    height: 6rem;
    display: block;

    @include respond(mobilemini) {
      width: 4rem;
      height: 4rem;
    }
}

.title {
    font-size: 2rem;
    @include respond(mobilemini) {
      font-size: 18px;
    }
}

.descr {
    color: $text-color;
    font-size: 1.6rem;
}

.welcome-page__title {
    font-size: 2.6rem;
    margin-bottom: 1.6rem;

    @include respond(mobilemini) {
      font-size: 18px;
    }
}

.welcome-page__descr {
    font-size: 1.6rem;

    @include respond(mobilemini) {
      font-size: 12px;
    }
}

/* Activate-page */

.activate-page {
    padding-bottom: 6.4rem;

    @include respond(mobilemini) {
      padding-top: 14px;
      padding-bottom: 0px;
    }
}

.activate-page__inner {
    max-width: 86rem;
    margin-right: auto;
    margin-left: auto;

    @include respond(phone) {
      max-width: 100%;
      margin-right: 0;
      margin-left: 0;
    }
}

.activate-page__header {
    margin-bottom: 4rem;

    @include respond(mobilemini) {
      margin-bottom: 1.6rem;
    }
}

.activate-page__title {
    font-size: 2.6rem;
    margin-bottom: 8px;

    @include respond(mobilemini) {
      font-size: 18px;
      margin-bottom: 6px;
    }
}

.active-account__item {
    transition: all 0.3s ease-in-out;
    cursor: pointer;
    padding: 2.4rem 4rem;
    position: relative;
    @include respond(mobilemini) {
      padding: 14px 18px 16px;
    }
}

.active-account__item:last-child {
    @include respond(mobilemini) {
      border-bottom: none;
    }
}

.active-account__item:hover {
    background-color: $box-hover;
}

.active-account__item:hover .click-page__title {
    color: $bckg-blue;
}

.clcik-page {
    min-height: 5.6rem;

    @include respond(mobilemini) {
        align-items: flex-start;
        min-height: auto;
    }
}
.click-page__numbers {
    display: flex;
}

.click-page__number {
    font-size: 2rem;
    font-weight: 700;
    width: 3.6rem;
    height: 3.6rem;
    background-color: $bckg-blue;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;

    @include respond(mobilemini) {
      width: 2rem;
      height: 2rem;
      font-size: 13px;
      flex-shrink: 0;
    }
}

.click-page__name {
    text-align: left;
    margin-left: 4rem;

    @include respond(mobilemini) {
      margin-left: 8px;
    }
}

.click-page__title {
    margin-bottom: 8px;

    @include respond(mobilemini) {
      font-size: 15px;
      margin-bottom: 5px;
    }
}

.click-page__icons {
    position: absolute;
    // top: 3rem;
    right: 4rem;

    @include respond(mobilemini) {
      right: 18px;
      top:  36%;
    }
}

.click-page__descr {
    @include respond(mobilemini) {
      margin-left: -2.5rem;
      display: block;
      font-size: 13px;
    }
}

.click-page__warning, .click-page__success {
    width: 2rem;
    height: 2rem;
    background-color: #E10909;
    border-radius: 50%;
    display: inline-block;
}

.icon-warning, .icon-success {
    margin-top: -5px;
    color: #fff;
}

.click-page__success {
    background-color: #24B63B;
}

.click-page__right {
    width: 2rem;
    height: 2rem;
    display: block;
}

.click-page__icons > .click-page__right, .click-page__warning, .click-page__success {
    display: none;
}

.click-page__icons .active {
    display: block;
}

.click-page__numbers > .click-page__number, .click-page__warning, .click-page__success {
    @include respond(mobilemini) {
      display: none;
    }
}

.click-page__numbers > .active {
    @include respond(mobilemini) {
        display: inline-block;
    }
}