.site-footer__dashboard {
  @include respond(phone) {
    background-color: #fff;
    padding-top: 2.4rem;
    padding-bottom: 2.4rem;
  }

  @include respond(mobilemini) {
    padding-top: 8px;
    padding-bottom: 1.6rem;
  }
}

.dashboard-menu__tab {
  display: none;
  @include respond(phone) {
    display: block;
  }
}

.dashboard-menu__list_tab {
  display: flex;
}

.dashboard-menu__item_tab {
  @include respond(phone) {
    margin-bottom: 0;
    padding: 0;
    width: 16%;
  }

  @include respond(mobtab) {
    width: 24%;
  }
}

.dashboard-menu__item_tab:not(:last-child) {
  @include respond(phone) {
    margin-right: 2rem;
  }

  @include respond(mobtab) {
    margin-right: 8px;
  }
}

.dashboard-menu__link_tab {
  display: flex;
  flex-direction: column;
}

.dashboard-menu__link_tab:hover .dashboard-menu__text_tab {
  color: #182e45;
}

.dashboard-menu__link_tab::after {
  display: none;
}

.dashboard-menu__icon_tab {
  margin-bottom: 12px;
  @include respond(mobilemini) {
    margin-bottom: 4px;
  }
}

.dashboard-menu__text_tab {
  margin-left: 0;
  @include respond(phone) {
    display: block;
  }

  @include respond(mobilemini) {
    font-size: 1rem;
  }
}

.dashboard-menu__item_mobile {
  @include respond(mobtab) {
    display: none;
  }
}
